import { Trans } from '@lingui/macro'
import React from 'react'

import { FMenuWareUnitFragment } from '../../../graph/generated'
import { formatPrice } from '../../../helpers/formats'
import { TouchButton } from '../../../molecules/TouchButton'

interface IProps {
  cardName: string
  wareUnit: Maybe<FMenuWareUnitFragment>
  quantity: number
  onConfirm(): void
}

export const StockDepositConfirmButton: React.FC<IProps> = ({
  wareUnit,
  quantity,
  onConfirm,
}) => (
  <TouchButton disabled={wareUnit === null} onClick={onConfirm}>
    {wareUnit ? (
      <Trans>{`Add ${formatPrice(quantity)} ${wareUnit.code}`}</Trans>
    ) : (
      <Trans>No stock card selected</Trans>
    )}
  </TouchButton>
)
