import React from 'react'

import {
  QStockForTransferStockItems,
  useQStockForTransfer,
} from '../../../graph/generated'
import { ARRAY_EMPTY } from '../../../helpers/form'
import { useTransferModel } from './useTransferModel'

export type TStockTransferItem = QStockForTransferStockItems

export function useStockTransferLoader() {
  const transfer = useTransferModel()

  const { getStash, loading } = useQStockForTransfer({
    branchId: transfer.sourceId,
  })

  const stockItems = getStash(data => data.stockItems)

  React.useEffect(() => {
    if (!stockItems) {
      return
    }
    const itemsToInit = stockItems.map(item => {
      const max =
        item.allowNegativeValue === true
          ? Number.MAX_SAFE_INTEGER
          : Math.max(item.quantity, 0)

      return { id: item.id, quantity: item.quantity, max }
    })

    transfer.initItems(itemsToInit)
  }, [stockItems, transfer])

  const filterItems = transfer.filter.getPredicate<TStockTransferItem>({
    exactMatch: ['plu'],
    partialMatch: ['code', 'nameLabel'],
  })

  return {
    isLoading: loading,
    filteredItems: stockItems ? stockItems.filter(filterItems) : ARRAY_EMPTY,
  }
}
